.show-sidebar-btn {
  position: fixed;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  padding: 14px;
  border: none;
  background-color: orangered;
  display: none;

  @media (max-width: 767px) {
    display: flex;
  }

  img {
    width: 100%;
  }
}
